import React from 'react';
import './app.css';
import twitter from './twitter.svg';
import twitch from './twitch.svg';
import soundcloud from './soundcloud.svg';
import youtube from './youtube.svg';

var glm = require('gl-matrix');
var Space3D = require('./space-3d.js');
var Skybox = require('./skybox.js');


var fov = 60;
fov = (fov / 360) * Math.PI * 2;
var pointStars = true;
var seed = (Math.random() * 1000000000000000000).toString(36);
var stars = true;
var sun = true;
var nebulae = true;
var resolution = 1024;
var animationSpeed = 0.015;
var unifiedTexture = true;
var renderCanvas = document.getElementById('render-canvas');
renderCanvas.width = window.innerWidth;
renderCanvas.height = window.innerHeight;
var skybox = new Skybox(renderCanvas);
var space = new Space3D(resolution);
var tick = 0.0;


class App extends React.Component {
  constructor(props) {
    super(props);

    this.renderSpace = this.renderSpace.bind(this);

    this.renderTextures();
    this.renderSpace();
  }

  hideUnified() {
    document.getElementById('texture-canvas').style.display = 'none';
  }

  hideSplit() {
    document.getElementById('texture-left').style.display = 'none';
    document.getElementById('texture-right').style.display = 'none';
    document.getElementById('texture-top').style.display = 'none';
    document.getElementById('texture-bottom').style.display = 'none';
    document.getElementById('texture-front').style.display = 'none';
    document.getElementById('texture-back').style.display = 'none';
  }

  renderTextures() {
    var textures = space.render({
      seed: seed,
      pointStars: pointStars,
      stars: stars,
      sun: sun,
      nebulae: nebulae,
      unifiedTexture: unifiedTexture,
      resolution: resolution,
    });
    skybox.setTextures(textures);
    var canvas = document.getElementById('texture-canvas');
    canvas.width = 4 * resolution;
    canvas.height = 3 * resolution;
    var ctx = canvas.getContext('2d');
    ctx.drawImage(textures.left, resolution * 0, resolution * 1);
    ctx.drawImage(textures.right, resolution * 2, resolution * 1);
    ctx.drawImage(textures.front, resolution * 1, resolution * 1);
    ctx.drawImage(textures.back, resolution * 3, resolution * 1);
    ctx.drawImage(textures.top, resolution * 1, resolution * 0);
    ctx.drawImage(textures.bottom, resolution * 1, resolution * 2);

    function drawIndividual(source, targetId) {
      var canvas = document.getElementById(targetId);
      canvas.width = canvas.height = resolution;
      var ctx = canvas.getContext('2d');
      ctx.drawImage(source, 0, 0);
    }

    drawIndividual(textures.left, 'texture-left');
    drawIndividual(textures.right, 'texture-right');
    drawIndividual(textures.front, 'texture-front');
    drawIndividual(textures.back, 'texture-back');
    drawIndividual(textures.top, 'texture-top');
    drawIndividual(textures.bottom, 'texture-bottom');
  }

  renderSpace() {
    this.hideUnified();
    this.hideSplit();

    tick += 0.0025 * animationSpeed;


    var view = glm.mat4.create();
    var projection = glm.mat4.create();

    renderCanvas.width = window.innerWidth;
    renderCanvas.height = window.innerHeight;

    glm.mat4.lookAt(view, [0, 0, 0], [Math.cos(tick), Math.sin(tick * 0.555), Math.sin(tick)], [0, 1, 0]);

    glm.mat4.perspective(projection, fov, renderCanvas.width / renderCanvas.height, 0.1, 8);

    skybox.render(view, projection);

    requestAnimationFrame(this.renderSpace)
  }

  render() {
    return (
      <div className="App">
        <div className="information">
          <p>OCTAVIA</p>
          <div className="links">
            <a href="https://twitter.com/octaviauwu" target="_blank" rel="noopener noreferrer"><img src={twitter} alt="Twitter Link"></img></a>
            <a href="https://www.twitch.tv/octayyvia" target="_blank" rel="noopener noreferrer"><img src={twitch} alt="Twitch Link"></img></a>
            <a href="https://www.soundcloud.com/octaviauwu" target="_blank" rel="noopener noreferrer"><img src={soundcloud} alt="SoundCloud Link"></img></a>
            <a href="https://www.youtube.com/channel/UCo5iJfS9rDGTNyZmSKKHy5w" target="_blank" rel="noopener noreferrer"><img src={youtube} alt="YouTube Link"></img></a>
          </div>
        </div>
        <div className="heart">
          <p></p>
        </div>
        <div className="project-button">
          <button className="button">PROJECTS</button>
        </div>
      </div>
    );
  }
}

export default App;
